@import url(https://fonts.googleapis.com/css?family=Montserrat:500&display=swap);
.ScoreCardContainer {
	display: flex;
	height: 100%;
	justify-content: center;
	align-items: center;
}

ul {
	list-style: none;
	display: block;
	padding: 0;
	margin: 0;
}

li {
	font-family: "Montserrat", sans-serif;
	color: rgb(235, 235, 235);
	font-size: 24px;
	padding-right: 30px;
	display: inline;
}

.Nav {
	height: 60px;
	background-color: #6002ee;
	margin-bottom: 0;
}

.IconCard {
	width: 125px;
	height: 125px;
	border: solid 1px rgb(255, 255, 255);
	border-radius: 10px;
	display: block;
	cursor: pointer;
	background: rgb(255, 255, 255);
	padding: 5px;
	box-shadow: 1px 1px 11px 3px rgba(0, 0, 0, 0.63);
}

.IconCard:hover {
	background-color: rgb(245, 245, 245);
	width: 127px;
	height: 127px;
}

@media only screen and (max-width: 450px) {
	.IconCard {
		width: 90px;
		height: 90px;
	}
}

@media only screen and (max-width: 450px) {
	.IconCard:hover {
		background-color: rgb(255, 255, 255);
		width: 90px;
		height: 90px;
	}
}

.CardWrapper {
	display: grid;
	margin-top: 50px;
	grid-template-columns: repeat(4, 137px);
	grid-template-rows: repeat(3, 137px);
	grid-gap: 4em;
}

@media only screen and (max-width: 890px) {
	.CardWrapper {
		grid-gap: 3em;
	}
}

@media only screen and (max-width: 720px) {
	.CardWrapper {
		grid-gap: 1em;
	}
}

@media only screen and (max-width: 720px) {
	.CardWrapper {
		grid-template-columns: repeat(3, 137px);
		grid-template-rows: repeat(4, 137px);
	}
}

@media only screen and (max-width: 450px) {
	.CardWrapper {
		grid-template-columns: repeat(3, 103px);
		grid-template-rows: repeat(4, 103px);
	}
}

.header {
	height: 120px;
	color: white;
	font-family: "Montserrat", sans-serif;
	display: flex;
	height: 100%;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
	background-color: #3e287e;
	box-shadow: -1px 13px 22px -16px rgba(0, 0, 0, 0.75);
}

.header h1 {
	color: rgb(205, 52, 52);
	margin: 0px;
}

.pageName {
	margin-top: 10px;
	display: flex;
	justify-content: center;
	width: 100%;
	margin-bottom: 8px;
}

.info {
	display: flex;
	justify-content: center;
	padding: 10px;
	width: 100%;
	margin-bottom: 8px;
}

@media only screen and (max-width: 450px) {
	.header h1 {
		font-size: 26px;
	}
}

.main {
  display: flex;
  justify-content: center;
}

